.app-wrapper {
    display: grid;
    width: 100%;
    margin: 0 auto;
    background-color: #edf7fb;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}


.app-wrapper-content {
    margin: 20px 20px 0 20px;
    border-radius: 5px;
    grid-area: c;
    background-color: #edf7fb;
    min-height: 100vh;
}





